
body {
  font-family: 'Lucida Sans Unicode', 'Arial', serif;
  font-size: larger;
  /* background-color: #1F2F48; */
  background-color: rgb(24, 49, 72);
  color: white;
  height: 100%;
  min-height: 100%;
}

html {
  height: 100%;
  scroll-behavior: smooth;
}

h1 {
  font-size: 80px;
  padding-top: 100px;
}

.btn {
  font-size: 20px;
  background-color: rgb(57, 104, 105);
  color: whitesmoke;
}

#root, .App {
  min-height: 100%;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 70px;
}

.home-header .headerShadow {
  width: 100%;
  height: 750px;
  position: absolute;
  display: block;
  background: -webkit-gradient(linear, left bottom, left top, from(rgba(24, 49, 72, 1)), color-stop(5%, rgba(24, 49, 72, 0.95)), color-stop(15%, rgba(24, 49, 72, 0.7)), color-stop(30%, rgba(24, 49, 72, 0.2)), color-stop(50%, rgba(24, 49, 72, 0)));
  background: linear-gradient(0deg, rgba(24, 49, 72, 1) 0%, rgba(24, 49, 72, 0.95) 5%, rgba(24, 49, 72, 0.7) 15%, rgba(24, 49, 72, 0.2) 30%, rgba(24, 49, 72, 0) 50%);
}

.headerBG {
  width: 100%;
  height: 750px;
  z-index: -1;
  position: absolute;
}

.home {
  top: 0;
  position: relative;
}

.home-header {
  height: 750px;
  top: 0px;
}

.intro-header {
  padding: 0px;
}

.about-header {
  height: 500px;
}

.App-title {
  font-size: 2.5em;
}

.App-intro {
  font-size: large;
}

.block {
  width: 130px;
  margin: 0 auto;
  display: block;
}

header .btn {
  text-transform: uppercase;
}

header a:hover, .btn a:hover::before {
  text-decoration: none;
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
  box-shadow: 0 0 15px teal;
}

header a:hover, .btn a:hover {
  color: white;
  box-shadow: 0 0 0 yellow;
  text-shadow: 0 0 5px white;
}

header .btn, .services {
  margin-top: 100px;
}

header {
  padding-bottom: 50px;
  background-size: cover;
  text-align: center;
}

.about, .contact {
  padding: 150px 0;
}

.projects {
  background-position: center;
  background-size: cover;
  background-position: 0 0;
  background-repeat: no-repeat;
  /* box-shadow: 0px -100px 900px 50px rgb(24, 49, 72); */
  padding-top: 100px;
  padding-bottom: 100px;
}

nav ul, .contact ul {
  list-style: none;
}

nav.navbar {
  position: absolute;
  width: 100%;
  text-align: right;
  right: 0;
  width: auto;
  padding-bottom: 100px;
  padding-right: 50px;
  color: white;
}

nav li {
  width: 100%;
}

nav a {
  position: center;
  width: 100%;
  height: 30px;
  padding: 10px 0;
  line-height: 30px;
}

nav li {
  width: auto;
  float: right;
  padding-top: 30px;
  padding-bottom: 50px;
}

a {
  box-shadow: white;
  color: white;
}

nav a {
  padding: 7px;
  border-right: 10px 1px;
  float: right;
  width: 100px;
  font-size: 18px;
}

footer {
  margin-top: 50px;
  padding-bottom: 20px;
  text-align: center;
}

.resume {
  width: 80%;
  text-align: center;
  padding: 0;
  margin-top: 50px;
  margin-left: auto;
  margin-right: auto;
}

.resume * {
  position: relative;
  margin-top: 50px;
  width: 80%;
  margin: auto;
}

.contacts {
  text-align: center;
  padding: 100px 100px 0 100px;
}

.contacts a {
  text-align: center;
  padding-left: 20px;
  padding-right: 20px;
}

section a:hover, .btn a:hover {
  color: yellow;
  box-shadow: 0 0 0 yellow;
  text-shadow: 0 0 5px black;
}

h2 {
  /* font-family: "SF Pro Text", "SF Pro Icons", "Helvetica Neue", "Helvetica", "Arial", sans-serif; */
  font-size: 40pt;
  padding-bottom: 25px;
}

h3 {
  font-size: 25pt;
  padding-bottom: 25px;
}

.pageTitle {
  margin-top: 50px;
}

.titleName {
  text-align: left;
  color: teal;
}

.aboutmeSection {
  padding-top: 150px;
  padding-left: 50px;
  background: -webkit-gradient(linear, left bottom, left top, color-stop(50%, rgba(24, 49, 72, 1)), color-stop(80%, rgba(24, 49, 72, 0.88)), color-stop(85%, rgba(24, 49, 72, 0.7)), to(rgba(0, 0, 0, 0)));
  background: linear-gradient(0deg, rgba(24, 49, 72, 1) 50%, rgba(24, 49, 72, 0.88) 80%, rgba(24, 49, 72, 0.7) 85%, rgba(0, 0, 0, 0) 100%);
}

.aboutme {
  padding-bottom: 50px;
  text-align: left;
}

.title {
  margin-top: 100px;
  text-align: middle;
}

.description {
  padding-left: 40px;
  padding-right: 40px;
}

img {
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 40%;
}

.projectsBox {
  text-align: center;
}

.projBGBG {
  position: absolute;
}

.projBGShadow {
  background: -webkit-gradient(linear, left bottom, left top, color-stop(50%, rgba(0, 0, 0, 0)), color-stop(85%, rgba(24, 49, 72, 0.7)), color-stop(91%, rgba(24, 49, 72, 0.88)), color-stop(99%, rgba(24, 49, 72, 1))), -webkit-gradient(linear, left bottom, left top, color-stop(5%, rgba(24, 49, 72, 1)), color-stop(20%, rgba(0, 0, 0, 0)));
  background: linear-gradient(0deg, rgba(0, 0, 0, 0) 50%, rgba(24, 49, 72, 0.7) 85%, rgba(24, 49, 72, 0.88) 91%, rgba(24, 49, 72, 1) 99%), linear-gradient(0deg, rgba(24, 49, 72, 1) 5%, rgba(0, 0, 0, 0) 20%);
  display: block;
  pointer-events: none;
}

.projBG {
  width: 100%;
  opacity: 0;
  z-index: -10;
}
.projectBox {
  background-size: cover;
  width: 100%;
  background:  no-repeat center;
  margin: 0;
  position: relative;
  display: inline-block;
  border-radius: 10%;
}

.projectName {
  color: white;
  border: white 3px solid ;
  text-align: center; 
  display: inline-block;
  padding: 5px 20px;
  background: rgba(255,255,255,0.4);
  font-size: 20pt;
  font-family: "Garamond", sans-serif;
  position: relative;
  top: 35%;
 }

.projectsBox {
  -webkit-transition: ease-in-out 0.8s;
  transition: ease-in-out 0.8s;
  position: relative;
  margin: 0;
  width: 80;
   
}

.projectBox {
  -webkit-transition: ease-in-out 0.3s;
  transition: ease-in-out 0.3s;
  background-size: contain;
  background-color: darkslategray;
}

.projectBox:hover {
  -webkit-transform: scale(1.15);
          transform: scale(1.15);
  opacity: 1 !important;
}

.projectsBox {
  pointer-events: none;
}

.projectsBox .projectBox {
  pointer-events: auto;
}

.projectsBox:hover .projectBox {
  opacity: 0.5;
}

.projectsBox:hover .projectBox:hover {
  opacity: 1;
}
 

.description{ 
  margin-top: 25px;
  margin-bottom: 50px ;


}
.bubble {
    /*background-color: #00b4ff;*/
    width: 200px;
    height: 200px;
    border-radius: 50%;
    z-index: 100;
    margin-left: -100px;
    margin-top: -100px;
    position: fixed;
    box-shadow: 0 20px 30px rgba(0, 0, 0, 0.2), inset 0px 10px 30px 5px rgba(255, 255, 255, 1);
    /*background: radial-gradient(ellipse at center, rgba(0,180,255, 0) 0%,rgba(255,255,255,0.5) 70%);*/
    pointer-events: none;
}

.bubble:after {
    background: radial-gradient(ellipse at center, rgba(0, 180, 255, 0.45) 0%, rgba(255, 255, 255, 0) 70%);
    box-shadow: inset 0 20px 30px rgba(255, 255, 255, 0.3);
    border-radius: 50%;
    content: "";
    height: 180px;
    left: 10px;
    position: fixed;
    width: 180px;
}
